import React from 'react';
import { Divider } from 'antd';

const AppDivider = (props) => {

  const dividerStyle = {
    borderColor: props.themeType,
    color: props.themeType
  }

  return (
    <div>
      {props.isHeading}
      <Divider style={dividerStyle} orientation="horizontal">
        {props.isHeading ? <h2>{props.text}</h2> : props.text}
      </Divider>
    </div>
  );
}

export default AppDivider;
