import React from 'react';
import { Card } from 'antd';
import { useTheme } from 'styled-components';
import AppDivider from '../AppDivider';

const UnderConstruction = () => {
  const theme = useTheme();
  const dividerText = 'Under construnction';

  const cardStyle = {
    background: theme.primaryDark,
    boxShadow: "rgba(0, 0, 0, 0.16) 0px 14px 28px, rgba(0, 0, 0, 0.23) 0px 10px 10px",
    borderRadius: '10px',
    color: theme.primaryLight,
    fontWeight: 500,
    fontSize: 'large'
  }

  return (
    <div>
      <AppDivider text={dividerText} themeType={theme.primaryDark} isHeading={false}/>
      <Card style={cardStyle}>
        <div style={{textAlign: "center"}}>
        <img  src="under-construction.gif" height="420" width="700"/>
        </div>
      </Card>
    </div>
  );
}

export default UnderConstruction;
