import React, { useState } from 'react';
import {
  Form, Input, Button, Col, Row, Modal, Spin 
} from 'antd';
import { TwitterWithCircle } from '@styled-icons/entypo-social';
import { Instagram } from '@styled-icons/entypo-social';
import { Facebook } from '@styled-icons/entypo-social';
import { SnapchatGhost } from '@styled-icons/fa-brands/SnapchatGhost';
import { PhoneCall } from '@styled-icons/boxicons-solid/PhoneCall';
import { Email } from '@styled-icons/material/Email';
import { LocationArrow } from '@styled-icons/fa-solid/LocationArrow';
import ReCAPTCHA from "react-google-recaptcha";

import { useTheme } from 'styled-components';

import AppDivider from '../AppDivider';
import { ReturnDownBack } from 'styled-icons/ionicons-sharp';

const heading = 'Contact';



const layout = {
  labelCol: {
    span: 6,
  },
  wrapperCol: {
    span: 16,
  },
};

const validateMessages = {
  required: '${label} is required!',
  types: {
    email: '${label} is not valid!',
    number: '${label} is not valid!',
  },
};


let verifyRecaptchaResp;
var verifyCallback = (response) => {
  console.log(response);
  verifyRecaptchaResp = response;
};

const Contact = () => {
  const theme = useTheme();
  const [form] = Form.useForm();
  let [isSpinning, setIsSpinning] = useState(false); 
  let [captcha, setCaptcha] = useState();

  const cardStyle = {
    background: theme.primaryDark,
    boxShadow: "rgba(0, 0, 0, 0.16) 0px 14px 28px, rgba(0, 0, 0, 0.23) 0px 10px 10px",
    borderRadius: '10px',
    color: theme.primaryLight,
    margin: 'auto',
    paddingTop: '2rem'
  }
  
  const buttonStyle = {
      background: theme.primaryButton,
      color: theme.primaryDark, 
      fontWeight: 'bold',
      backgroundColor: theme.primaryButton,
      border: 'none',
  }  

  const mainDividerProps = {
    isHeading: true,
    themeType: theme.primaryDark,
    text: 'Contact'
  };

  const subDividerProps = {
    isHeading: false,
    themeType: theme.primaryLight,
    text: 'Connect on'
  };

  const onFinish = (values) => {
    if(!verifyRecaptchaResp || verifyRecaptchaResp.trim().length === 0) {
      Modal.error({
        content: 'Please verify CAPTCHA'
      })
      return
    } 
    setIsSpinning(true);
    values.user['g-recaptcha-response'] = verifyRecaptchaResp;
    values.user.subject = 'New Email from Value for Money';
    var headers = new Headers();
    headers.append("Content-Type", "application/x-www-form-urlencoded; charset=utf-8");
    fetch('https://script.google.com/macros/s/AKfycbxh81BGPxmN8SZ4LXtn5euzagf7V9uLjXELQDPYoMqYRGeh5RUh/exec', {
      method: 'post',
      headers: headers,
      body: JSON.stringify(values.user)
    }).then(function (response) {
      setIsSpinning(false);
      form.resetFields();
      captcha.reset();
      Modal.success({
        content: 'Thanks for your interest! We will contact you soon',
      });
    }).catch((error) => {
      setIsSpinning(false);
      Modal.error({
        content: 'There is an error sending your information!',
      });
    });
  };

  return (

    <div className="site-card-wrapper"   >
      <AppDivider {...mainDividerProps} ></AppDivider>
      <Spin spinning={isSpinning} size="large">
      <Row gutter={[0, 24]} style={cardStyle} >
        <Col xs={{span: 24}} sm={{span: 24}} md={{span: 14}} lg={{span: 14}}>
        
          <Form {...layout} name="contact" onFinish={onFinish} validateMessages={validateMessages} form={form}>
            <Form.Item name={['user', 'name']} messageVariables={{ label: 'Name' }} label={<label style={{ color: "rgba(255, 255, 255, 0.65)" }}>Name</label>}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              name={['user', 'email']} messageVariables={{ label: 'Email' }}
              label={<label style={{ color: "rgba(255, 255, 255, 0.65)" }}>Email</label>}
              rules={[
                {
                  required: true,
                  type: 'email',
                },
              ]}
            >
              <Input />

            </Form.Item>
            <Form.Item
              name={['user', 'phone']} messageVariables={{ label: 'Phone number' }}
              label={<label style={{ color: "rgba(255, 255, 255, 0.65)" }}>Phone Number</label>}
              rules={[
                { required: true, message: 'Please input your phone number!' }]}
            >
              <Input />
            </Form.Item>
            <Form.Item name={['user', 'message']} label={<label style={{ color: "rgba(255, 255, 255, 0.65)" }}>Message</label>}>
              <Input.TextArea style={{ height: '10em' }} />
            </Form.Item>

            <Form.Item label={<label style={{ color: "rgba(255, 255, 255, 0.65)"}}>Verify Captcha</label>}>
              <ReCAPTCHA 
                sitekey="6LcuiKUaAAAAAO5gZfbTydAPXhO1MWZ5pSLePED3"
                onChange={verifyCallback}
                ref={setCaptcha}
              />
            </Form.Item>

            <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 12 }}>
              <Button htmlType="submit" style={buttonStyle} type="primary" shape="round" >
                <strong>Send</strong>
                </Button>
            </Form.Item>
          </Form>
          
        </Col>
        <Col xs={{span: 24}} sm={{span: 24}} md={{span: 8}} lg={{span: 8}}>
          <Row style={{ paddingBottom: '1rem' }}>
            <Col  style={{ paddingRight: '1.5rem' }}>
                <LocationArrow size="15"/>
            </Col>
            <Col >
              Office No 221, 2nd Floor, Chetak Chember,<br/>
              Next to Dawa Bazar Indore - 452001
            </Col>
          </Row>
          <Row style={{ paddingBottom: '1rem' }}>
            <Col  style={{ paddingRight: '1.5rem' }}>
              <PhoneCall size="15"/>
            </Col>
            <Col  style={{ paddingRight: '1.5rem' }}>
              +91-8871716111
            </Col>
          </Row>
          <Row>
            <Col  style={{ paddingRight: '1.5rem' }}>
              <Email size="15"/>
            </Col>
            <Col  style={{ paddingRight: '1.5rem' }}>
              valueformoneyconsultants@gmail.com
            </Col>
          </Row>
          <AppDivider {...subDividerProps}></AppDivider>
          <Row>
            <Col span={24} style={{ textAlign: 'center' }}>
              <a href="https://www.facebook.com" target="_blank" rel="noopener noreferrer"><Facebook size="25"></Facebook></a> &nbsp;&nbsp;&nbsp;
                <a href="https://www.twitter.com" target="_blank" rel="noopener noreferrer"><TwitterWithCircle size="25"></TwitterWithCircle></a> &nbsp;&nbsp;&nbsp;
                <a href="https://www.instagram.com" target="_blank" rel="noopener noreferrer"><Instagram size="25"></Instagram></a> &nbsp;&nbsp;&nbsp;
                <a href="https://www.snapchat.com" target="_blank" rel="noopener noreferrer"><SnapchatGhost size="25"></SnapchatGhost></a>
            </Col>
          </Row>
        </Col>
      </Row>
      </Spin>
    </div>
  )
}

export default Contact;