
import React from 'react';
import { StyledWidget, StyledCard } from './Widget.styled';
import {
  Link
} from 'react-router-dom';
import {
  Form, Image, Button, Col, Row, Modal, Card
} from 'antd';

const Widget = ({ name, link, icon, color, text }) => {
  const cardStyle = {
    boxShadow: "rgba(0, 0, 0, 0.16) 0px 14px 28px, rgba(0, 0, 0, 0.23) 0px 10px 10px",
    borderRadius: '10px',
    backgroundColor: color,
    height: '9rem',
  }

  return (
    <Col xs={{span: 12}} sm={{span: 12}} md={{span: 12}} lg={{span: 6}}>
      <div>
        <Link to={link}>
          <StyledCard>
            <Card bordered={false} size="small" hoverable={true} style={cardStyle}>
              <h3 style={{ color: 'black', fontSize: '1.2rem' }}>{name}</h3>
              {text}
          </Card>
          </StyledCard>
        </Link>
      </div>
    </Col>
  )
}

export default Widget;