import React from 'react';
import { StyledLogo } from './Logo.styled';
import { Link } from 'react-router-dom';

const Logo = () => {
  return (
    <StyledLogo>
       <Link to="/"> <img alt="logo" width="50" height="40" src="favicon.ico" /></Link>
    </StyledLogo>
  )
}

export default Logo;
