import React from 'react';
import Logo from '../Logo';
import { Menu } from 'antd';
import { HomeOutlined } from '@ant-design/icons';
import { Row, Col } from 'antd';
import { Link } from 'react-router-dom';

const { SubMenu } = Menu;

const AppHeader = () => {
  const [current, setCurrent] = React.useState([])

  const handleClick = e => {
    setCurrent({ current: e.key });
  };

  Menu.Item.isMenuItem = 1;
  return (
    <Row>
      <Col flex={1}>
        <Logo></Logo>
      </Col>
      <Col flex={4}>
        <Menu onClick={handleClick} selectedKeys={[current]} theme="dark" mode="horizontal"
          style={{ float: 'right' }}>
          <Menu.Item key="mail" icon={<HomeOutlined />}>
            <Link to="/">Home</Link>
          </Menu.Item>
          <Menu.Item key="about">
            <Link to="/about">About</Link>
          </Menu.Item>
          <SubMenu key="services" title="Services">
            <SubMenu key="accountingService" title="Accounting Service">
              <Menu.Item key="financialAccounting">
                <Link to="/contact">Financial Accounting</Link>
              </Menu.Item>
              <Menu.Item key="managementAccounting">
                <Link to="/contact">Management Accounting</Link>
              </Menu.Item>
              <Menu.Item key="auditing">
                <Link to="/contact">Auditing</Link>
              </Menu.Item>
              <Menu.Item key="taxAccounting">
                <Link to="/contact">Tax Accounting</Link>
              </Menu.Item>
              <Menu.Item key="costAccounting">
                <Link to="/contact">Cost Accounting</Link>
              </Menu.Item>
              <Menu.Item key="payrollServices">
                <Link to="/contact">Payroll Services</Link>
              </Menu.Item>
              <Menu.Item key="statutoryServices">
                <Link to="/contact">Statutory Services</Link>
              </Menu.Item>
              <Menu.Item key="virtualAccounting">
                <Link to="/contact">Virtual Accounting</Link>
              </Menu.Item>
              <Menu.Item key="professionalAccounting">
                <Link to="/contact">Professional Accounting</Link>
              </Menu.Item>
              <Menu.Item key="taxPlanning">
                <Link to="/contact">Tax Planning</Link>
              </Menu.Item>
              <Menu.Item key="consultingService">
                <Link to="/contact">Consulting Service</Link>
              </Menu.Item>
            </SubMenu>
            <SubMenu key="rocCompliances" title="ROC Compliances">
              <Menu.Item key="companyAnnualFiling">
                <Link to="/contact">Company Annual Filing</Link>
              </Menu.Item>
              <Menu.Item key="llpAnnualFiling">
                <Link to="/contact">LLP Annual Filing</Link>
              </Menu.Item>
              <Menu.Item key="maintenanceOfMinutes">
                <Link to="/contact">Maintenance of Minutes</Link>
              </Menu.Item>
              <Menu.Item key="updationOfRecords">
                <Link to="/contact">Updation of Records</Link>
              </Menu.Item>
              <Menu.Item key="allotmentOfShares">
                <Link to="/contact">Allotment of Shares</Link>
              </Menu.Item>
              <Menu.Item key="changeRegOffice">
                <Link to="/contact">Change Registered Office</Link>
              </Menu.Item>
              <Menu.Item key="changeDirectors">
                <Link to="/contact">Change in Directors</Link>
              </Menu.Item>
              <Menu.Item key="changeAuthCapCompany">
                <Link to="/contact">Change Authorized Capital of Company</Link>
              </Menu.Item>
            </SubMenu>
          </SubMenu>
          <SubMenu key="tax" title="Tax">
            <Menu.ItemGroup>
              <Menu.Item key="incomeTaxReturn">
                <Link to="/under-construction">Income Tax Return</Link>
              </Menu.Item>
              <Menu.Item key="tdsReturn">
                <Link to="/under-construction">TDS Return</Link>
              </Menu.Item>
            </Menu.ItemGroup>
          </SubMenu>
          <SubMenu key="registration" title="Registration">
            <Menu.ItemGroup>
              <Menu.Item key="gstregistration">
                <Link to="/under-construction">GST Registration</Link>
              </Menu.Item>
              <Menu.Item key="compregistration">
                <Link to="/under-construction">Company Registration</Link>
              </Menu.Item>
              <Menu.Item key="patentregistration">
                <Link to="/under-construction">Patent Registration</Link>
              </Menu.Item>
              <Menu.Item key="propfirmregistration">
                <Link to="/under-construction">Proprietorship Firm Registration</Link>
              </Menu.Item>
            </Menu.ItemGroup>
          </SubMenu>
          <SubMenu key="license" title="License">
            <Menu.ItemGroup>
              <Menu.Item key="foodlicense">
                <Link to="/under-construction">Food License</Link>
              </Menu.Item>
              <Menu.Item key="druglicense">
                <Link to="/under-construction">Drug License</Link>
              </Menu.Item>
            </Menu.ItemGroup>
          </SubMenu>
          <Menu.Item key="contact">
            <Link to="/contact">Contact</Link>
          </Menu.Item>
        </Menu>
      </Col>
    </Row>
  );
}

export default AppHeader;
