import React, { useState } from 'react';
import Widget from '../Widget';
import { WidgetItems } from '../Widget';
import {
  Carousel,
  Row,
} from 'antd';
import { LeftCircleOutlined, RightCircleOutlined } from '@ant-design/icons';

/* Container holding the image and the text */
const container = {
  position: 'relative',
  textAlign: 'center',
  width: '100%'
};

/* Bottom right text */
const textBlock = {
  position: 'absolute',
  top: '7rem',
  paddingLeft: '25rem',
  color: 'black',
  fontFamily: 'Times new roman',
  fontSize: '1.5rem',
  textShadow: '0 0 2px black, 0 0 4px green',
  
};

const Home = () => {
  let [carousel, setCarousel] = useState(null)

  const SampleNextArrow = (props) => {
    const { className, style, onClick } = props
    return (
      <div
        className={className}
        style={{
          display: "inline-block", border: 'solid black', borderWidth: '0 3px 3px 0'
          , transform: 'rotate(-45deg)'
        }}
        onClick={onClick}
      ></div>
    )
  }

  const SamplePrevArrow = (props) => {
    const { className, style, onClick } = props
    return (
      <RightCircleOutlined
        className={className}
        style={{
          ...style, display: "inline-block", border: 'solid black', borderWidth: '0 3px 3px 0'
          , transform: 'rotate(135deg)'
        }}
        onClick={onClick}
      />
    )
  }

  const settings = {
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
  }

  return (
    <div style={{ textAlign: 'center', marginTop: '1rem' }}>
      <Carousel  autoplay arrows ref={setCarousel} {...settings}>
        <div style={container}>
          <img src="grow-money.jpg" height="350vh" width="1220vw" style={{width: '100%'}}></img>
          <div style={textBlock}>
           {/**  <p style={{paddingLeft: '15rem'}}>Grow your money<br />
            with us!!</p>*/}
          </div>
        </div>
        <div style={container}>
        <img src="accounting.jpg" height="350vh" width="1220vw"  style={{width: '100%'}}></img>
          <div style={textBlock}>
           {/** <p style={{top: '12rem', textShadow: '0 0 2px black, 0 0 4px black'}}>Get expert assistence<br />
              </p> */}
          </div>
        </div>
        <div style={container}>
        <img src="law.jpg" height="350vh" width="1220vw"  style={{width: '100%'}}></img>
          <div style={textBlock} >
           {/**  <p style={{top:'12rem'}}>Got Law conflicts? <br/>Get it resolved with experts!!</p> */}
          </div>
        </div>
      </Carousel>
        <Row gutter={[24, 24]} style={{ marginTop: '1rem' }} >
          {WidgetItems.map((x, i) =>
            <Widget key={i} name={x.name} link={x.link} icon={x.icon} color={x.color} text={x.text}></Widget>)
          }
        </Row>
    </div>
  )
}

export default Home;
