import React from 'react';
import { Card } from 'antd';
import { useTheme } from 'styled-components';
import AppDivider from '../AppDivider';

const About = () => {
  const theme = useTheme();
  const dividerText = 'About us';

  const cardStyle = {
    background: theme.primaryDark,
    boxShadow: "rgba(0, 0, 0, 0.16) 0px 14px 28px, rgba(0, 0, 0, 0.23) 0px 10px 10px",
    borderRadius: '10px',
    color: theme.primaryLight,
    fontWeight: 500,
    fontSize: 'large'
  }

  return (
    <div>
      <AppDivider text={dividerText} themeType={theme.primaryDark} isHeading={true}/>
      <Card style={cardStyle}>
        <p>Value for Money is a firm which helps individual and companies to solve legal and corporate issues.
        We have been helping new businesses to solve legal and compliance issues in the journey of
        starting to fully establishment of the business. Our goal is to offer reachable,
          fast and automated consulting services to clients.</p>
        <p>Our mission is to provide one-click access to individuals and businesses for all their legal and professional needs</p>
        <p>Purushottam Bhandiya and Renuka Bhandiya are founder of Value for Money. Purushottam Bhandiya is a CS and has also done Law(LLB). He has over 10+ years of corporate and legal consulting experience with top firms.</p>
      </Card>
    </div>
  );
}

export default About;
