import styled from 'styled-components';

export const StyledWidget = styled.div`
display: inline-block;
text-decoration: none;
text-align: center;
a {
    display: inline-block;
    border-radius: 50%;
    padding: 1.5rem 0;
    margin: 0.5rem 1rem;
    width: 8rem;
    height: 8rem;
    background: ${({ theme }) => theme.primaryLight};
    background-size: 100% 100%, 50% 50%, 50% 50%, 50% 50%, 50% 50%;
    background-repeat: no-repeat;
  --  background-image: linear-gradient(${({ color }) => color}, ${({ color }) => color});
    background-image: linear-gradient(${({ color }) => color}, ${({ color }) => color}), 
                      linear-gradient(120deg, yellow 56%, lightgrey 40%),
                      linear-gradient(240deg, blue 56%, lightgrey 40%),
                      linear-gradient(360deg, green 56%, lightgrey 40%);
    background-position: center center, left top, right top, left bottom, right bottom;
    background-origin: content-box, border-box, border-box, border-box, border-box;
    background-clip: content-box;
   -- transform: rotate(30deg);
   -- border: 2px solid white;
    border: 4px solid transparent;
    color: white;
    text-decoration: none;
    font-family: Helvetica, Arial, sans-serif;
    text-align: center;
    box-shadow: 0 4px 4px #888888;
    transition: color 0.3s, background-image 0.5s, linear, width 4s;

    &:hover {
        background-image: linear-gradient(#b1ccda 49%, #96b4c5 51%);
        transform: scale(1.05);
        color: #03324c;
        cursor: pointer
    }

}

`
export const StyledCard = styled.div`
-- background: white;
-- border: 2px solid #DD4814;
-- background-clip: content-box;
-- border-radius: 10px;
-- padding: 2px;
&:hover {
    transform: scale(1.05);
  --  border: 0.1px solid black;
  --  border: 0.5px solid black;
  --  border-radius: 10px;
  --  animation: bounce 1.5s linear;
}

`